import React from 'react'
import Helmet from 'react-helmet'
import Link from 'gatsby-link'

const Project3 = () => (
  <div className="page Project3-page">
    <Helmet
      title="Udemy Free Courses Bot, Kacper Cieciński Junior Web Developer"
      meta={[
        { name: 'description', content: 'Free Udemy Courses Bot, by Kacper Ciecinski' },
        { name: 'keywords', content: 'Kacper, Cieciński, Kacper Cieciński, Ciecinski, developer, junior developer,junior react developer, junior front-end developer,front-end,frontend,backend,junior backend developer,junior rails developer,React,Ruby on Rails,Javascript, HTML5, CSS, Bot, Messenger Bot, Udemy Free Courses,Udemy Free Courses Bot, rails bot, facebook-messenger project' },
      ]}
    />

    <h1>Udemy Free Courses Bot</h1>
    <div className="row row-center">
      <div className="col-md-10">
      <p>Personal project, Messenger bot that sends link to free Udemy courses everyday. It's written in Ruby on Rails with facebook-messenger gem<br/>
         I managed to publish it on Messenger and now it's used by nearly 500 users everyday <br/>
         I learned how to use  and integrate your app with Facebook api and also how to deploy apps to Facebook ecosystem <br/>
      </p>
      <div className="info-container">
        <p>
          <a target="_blank" href="https://www.messenger.com/t/1340409259349307"><i className="fa fa-th-large"></i>Live Demo</a>
          <a target="_blank" href="https://github.com/kciecinski/messenger-bot"><i className="fa fa-github"></i>Github</a>
        </p>
      </div>
      <Link className="project-link" to="/projects">Back to projects</Link>

      </div>
    </div>

  </div>
)

export default Project3


